import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Switch from '@material-ui/core/Switch';
import Grid from '@material-ui/core/Grid';
import WebIcon from '@material-ui/icons/Web';
import MailIcon from '@material-ui/icons/Mail';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Header from '../../components/Header';
import Loading from '../../components/Loading';
import Storage from '../../utils/storage';
import styles from './styles';

const defaultState = {
  newsletters: [],
  selected: [],
  isCheckedAll: false,
  loading: false
};

class Newsletters extends PureComponent {
  static propTypes = {
    classes: PropTypes.object,
    theme: PropTypes.object
  };

  constructor(props) {
    super(props);
    this.state = defaultState;
  }

  async componentDidMount() {
    this.loadNewsletters();
  }

  loadNewsletters = async () => {
    this.setState({ loading: true });
    try {
      const newsSelected = await Storage.get('selected');
      const newsletters = await Storage.get('newsletters');
      const selected = _.isArray(newsSelected) ? newsSelected : [];
      const isCheckedAll = _.size(selected) >= _.size(newsletters);
      this.setState({ newsletters, selected, isCheckedAll, loading: false });
    } catch (err) {
      this.setState(defaultState);
    }
  }

  onToggleNewsletter = async (newsletterId, isChecked) => {
    const { newsletters } = this.state;
    const newsSelected = await Storage.get('selected');
    let selected = _.isArray(newsSelected) ? newsSelected : [];
    if (isChecked) {
      selected.push(newsletterId);
    } else {
      selected = _.reject(selected, s => s === newsletterId);
    }
    selected = [...new Set(selected)];
    const isCheckedAll = _.size(selected) >= _.size(newsletters);
    await Storage.set('selected', selected);
    this.setState({ selected, isCheckedAll });
  }

  onToggleAll = async () => {
    const { isCheckedAll, newsletters } = this.state;
    const selected = isCheckedAll ? [] : _.map(newsletters, n => n.id);
    await Storage.set('selected', selected);
    this.setState({ selected, isCheckedAll: !isCheckedAll });
  }

  render() {
    const { classes, theme: { mainColor } } = this.props;
    const { newsletters, loading, selected, isCheckedAll } = this.state;
    return (
      <React.Fragment>
        <Header title="Newsletters" />
        <main className={classes.layout}>
          {
            loading ?
              <Loading />
              :
              <section>
                {
                  _.size(newsletters) ?
                    <List component="nav">
                      <ListItem button onClick={() => this.onToggleAll()}>
                        <ListItemIcon>
                          <MailIcon fontSize="large" />
                        </ListItemIcon>
                        <ListItemText primary="Select all" />
                        <ListItemSecondaryAction>
                          <Switch
                            color={mainColor}
                            checked={isCheckedAll}
                            onChange={() => this.onToggleAll()}
                          />
                        </ListItemSecondaryAction>
                      </ListItem>
                      {
                        newsletters.map((n) => {
                          const checked = selected.includes(n.id);
                          return (
                            <ListItem
                              button
                              key={n.id}
                              onClick={() => this.onToggleNewsletter(n.id, !checked)}
                            >
                              <ListItemIcon>
                                <img
                                  src={require(`../../assets/images/icon/${n.id}.png`)}
                                  width={classes.icon.width}
                                  height={classes.icon.height}
                                  className={classes.icon}
                                  alt={n.name}
                                />
                              </ListItemIcon>
                              <ListItemText primary={n.name} />
                              <ListItemSecondaryAction>
                                <Switch
                                  color={mainColor}
                                  checked={checked}
                                  onChange={() => this.onToggleNewsletter(n.id, !checked)}
                                />
                              </ListItemSecondaryAction>
                            </ListItem>
                          );
                        })
                      }
                    </List>
                    :
                    <Grid
                      container
                      direction="column"
                      justify="center"
                      alignItems="center"
                      className={classes.empty}
                    >
                      <WebIcon fontSize="large" />
                      <Typography variant="h6">No newsletters in our feed</Typography>
                    </Grid>
                }
              </section>
          }
        </main>
      </React.Fragment>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Newsletters);
